.container {
    max-width: 1700px;
    margin-left: 15%;
}

.card {
    padding: 20px;
    margin: -35px;
    border-radius: 30px;

    border: 4px solid #ffffff;
}

.grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.item {
    display: flex;
    align-items: center;
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    transition: transform 0.3s;
}

.item:hover {
    transform: scale(1.05);
}

.circle {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
    width: 100px;
    border-radius: 50%;
}

.circle img {
    height: 50px;
}

.label {
    color: #b589df;
    font-size: 11px;
    font-weight: bold;
    line-height: 17px;
}

.value {
    display: block;
    font-size: 30px;
    font-weight: bold;
    color: black;
    margin-top: 5px;
}

.change {
    background-color: #ffffff;
    margin-top: 2px;
    color: green;
    font-size: 12px;
}

.change a {
    color: black;
}

.purple-text {
    color: #b589df;
}

.green-text {
    color: green;
}

.red-text {
    color: red;
}

.bg-purple {
    background-color: #e1feee;
}

.bg-light-purple {
    background-color: #eae4ff;
}

.bg-light-blue {
    background-color: #cdf3ff;
}

.bg-pink {
    background-color: #ef7694;
}

.table-charts {
    display: flex;
}

.custom-component {
    flex-basis: 50%;
    flex-direction: column;
    height: 100%;
    padding: 20px;
    border-radius: 30px;
    background-color: white;
    border: 10px solid whitesmoke;

}

.custom-component h1 {
    color: #000;
    font-size: 25px;
    line-height: 17px;
    margin-top: 5px;
    font-weight: bold;

}

.custom-component p {
    font-size: 12px;
    margin-left: 1em;
    margin-top: 1px;
}

.main-container {
    background: #FFFFFF;
    color: #abafc6;
    border-radius: 5px;
    padding: 25px;
    height: 350px;
    border-top: 2px solid whitesmoke;
}

.year-stats {
    white-space: nowrap;
    max-height: 400px;
    overflow: hidden;
}

.month-group {
    cursor: pointer;
    max-width: 500px;
    height: 135px;
    margin: 25px;
    display: inline-block;
}

.bar {
    background-color: #F2EFFF;
    width: 35px;
    border-radius: 5px;
    margin-bottom: 20px;
}

.month-group:hover .bar,
.selected .bar {
    background: #5A32EA;
}


.h-25 {
    height: 100%;
}

.h-50 {
    height: 150%;
}

.h-75 {
    height: 135%;
}

.h-100 {
    height: 175%;
}

.stats-info {
    margin-top: 15px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: relative;
}

.month {
    font-size: large;
    color: black;
}

.pie-component {
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
    height: 100%;
    margin-bottom: 4px;
    margin-top: 2px;
    padding: 20px;
    border-radius: 30px;
    background-color: #f4f5f7;
    border: 4px solid #ffffff;
}

.pie-component h1 {
    color: #000;
    font-size: 25px;
    line-height: 17px;
    margin-top: 5px;
    font-weight: bold;
}

.charts-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}



.pie-chart {
    margin-top: 20px;
}


h1 {
    font-size: 24px;
    margin-bottom: 20px;

}

table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}



th,
td {
    padding: 20px 7px;
    text-align: left;
    color: #000;
}

th {
    font-weight: bold;
}



tbody tr:hover {
    background-color: #f2f2f2;
}

.buttons-container {
    margin-left: 60%;
    margin-bottom: -3%;

}

.buttons-container button {
    background-color: #ffffff;
    padding: 10px 20px;
    border-radius: 5px;
    margin-right: 15px;
    font-size: 12px;
    border: 2px solid whitesmoke;
    /* Adjust the value as needed */
}

.buttons-container {
    display: flex;
}

.square {
    width: 20px;
    height: 20px;
    margin-right: 10px;
    background-color: #02bb7d;
    margin-left: 5%;
    /* Replace with your desired color */
}

.squares {
    width: 20px;
    height: 20px;
    margin-right: 10px;
    background-color: #47b746;
    margin-left: 5%;
    /* Replace with your desired color */
}

.button-container {
    margin-left: 60%;
    margin-bottom: -3%;

}

.button-container button {
    background-color: #e8eefd;
    padding: 10px 20px;
    border-radius: 5px;
    margin-right: 15px;
    font-size: 15px;
    /* Adjust the value as needed */
    color: #65b586;
    border: none;
}

