/* For screens smaller than 600px */
@media (max-width: 600px) {
    .container {
        max-width: 300px;
    }

    .card {
        padding: 20px;
        margin: 20px;
        border-radius: 30px;
    }

    .grid {
        grid-template-columns: 1fr;
        display: grid;
        gap: 10px;
    }

    .item {
        padding: 5px;
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
        border-radius: 30px;
        transition: transform 0.3s;
    }

    .circle {
        height: 80px;
        width: 80px;
    }

    .circle img {
        height: 40px;
    }

    .label {
        font-size: 14px;
        line-height: 20px;
    }

    .value {
        font-size: 24px;
    }

    .change {
        font-size: 10px;
    }


    .custom-component {
        flex-basis: 100%;
        display: flex;
        flex-direction: column;
        height: 100%;
        margin-bottom: 4px;
        margin-top: 2px;
        padding: 20px;
        border-radius: 30px;
        background-color: #f4f5f7;
        border: 4px solid #ffffff;
    }

    .custom-component p {
        font-size: 7px;
        margin-left: 1em;
        margin-top: 1px;
    }


    .main-container {
        border-radius: 5px;
        padding: 2px;
        height: 200px;
    }

    .year-stats {
        white-space: nowrap;
        max-height: 400px;
        overflow: hidden;
    }

    .month-group {
        cursor: pointer;
        max-width: 50px;
        height: 200px;
        margin: 2px;
        display: inline-block;
    }

    .bar {
        background-color: #F2EFFF;
        width: 15px;
        border-radius: 5px;
        margin-bottom: 2px;
    }

    .h-25 {
        height: 75%;
    }

    .h-50 {
        height: 65%;
    }

    .h-75 {
        height: 55%;
    }

    .h-100 {
        height: 65%;
    }


    .stats-info {
        margin-top: 15px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        position: relative;
    }

    .month {
        font-size: large;
        color: black;
    }

    .table-charts {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .table-charts .custom-component,
    .table-charts .pie-component {
        width: 100%;
        max-width: 400px;
    }

    .pie-chart-container {
        max-width: 100%;
        margin: 0 auto;
    }

    .pie-chart-container>svg {
        width: 100%;
        height: auto;
    }

    .pie-chart-container>div>svg {
        width: 100% !important;
        height: auto !important;
    }





}


